import './App.css';
import * as React from 'react';
import Grid from '@mui/material/Grid';
import Backdrop from '@mui/material/Backdrop';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import CircularProgress, {
    CircularProgressProps,
} from '@mui/material/CircularProgress';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Button from '@mui/material/Button';

const initialMessages = [
    "Feedback develops writing skills for academic and professional success",
    "Feedback tailors assignments to meet professor's expectations for better grades",
    "Feedback improves critical thinking skills and leads to better decision-making"
]

const loginTapped = (setCurrentPage, setStudyGroup, setEvaluationGroup, setAllIdeas) => {
    let username = document.getElementById('username').value;
    let password = document.getElementById('password').value + '';
    if (username == '') {
        alert('Please enter a username.');
        return;
    }
    if (password == '') {
        alert('Please enter a password.');
        return;
    }
    if (password.length != 2) {
        alert('Incorrect password. Please try again.');
        return;
    }
    let studyGroup = 1;
    if (password.startsWith('1')) {
        studyGroup = 1;
        setAllIdeas([]);
    } else if (password.startsWith('2')) {
        studyGroup = 2;
        setAllIdeas([]);
    } else if (password.startsWith('3')) {
        studyGroup = 3;
        setAllIdeas([initialMessages[0] + ".", initialMessages[1] + ".", initialMessages[2] + "."]);
    } else if (password.startsWith('4')) {
        studyGroup = 4;
        setAllIdeas([initialMessages[0] + ".", initialMessages[1] + ".", initialMessages[2] + "."]);
    } else {
        alert('Incorrect password. Please try again.');
        return;
    }

    let evaluationGroup = 1;
    if (password.endsWith('1')) {
        evaluationGroup = 1;
    } else if (password.endsWith('2')) {
        evaluationGroup = 2;
    } else if (password.endsWith('3')) {
        evaluationGroup = 3;
    } else if (password.endsWith('4')) {
        evaluationGroup = 4;
    } else {
        alert('Incorrect password. Please try again.');
        return;
    }

    localStorage.setItem('username_writingtheory', username);

    setTimeout(() => {
        setStudyGroup(studyGroup);
        setTimeout(() => {
            setEvaluationGroup(evaluationGroup);
            setTimeout(() => {
                setCurrentPage('study');
            }, 100);
        }, 100);
    }, 100);
}

function LoginPage({setCurrentPage, setStudyGroup, setEvaluationGroup, setAllIdeas}) {
    return (
        <>
            <div className='login-page-container'>
                <Grid container>
                    <Grid item xs={12} style={{ backgroundColor: 'transparent', padding: '8%', paddingBottom: '8%', borderRadius: 20 }}>
                        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                            <h1 style={{ fontSize: '300%', color: 'black' }}>Welcome</h1> <br />

                            {false ? <></> : <>

                            <span style={{ color: 'black', fontSize: 20 }}>
                                Username
                            </span> <br />
                            <input type="text" id="username" name="username" style={{ width: 490, height: 30, borderRadius: 5, resize: 'none', outline: 'none', fontSize: '110%', textAlign: 'center', padding: 5, borderColor: 'rgb(100, 236, 150)', border: 'solid', borderWidth: 3 }} /> <br />
                            <span style={{ color: 'black', fontSize: 20 }}>
                                Password
                            </span> <br />
                            <input type="password" id="password" name="password" placeholder="" style={{ width: 490, height: 30, borderRadius: 5, resize: 'none', outline: 'none', fontSize: '110%', textAlign: 'center', padding: 5, borderColor: 'rgb(100, 236, 150)', border: 'solid', borderWidth: 3 }} />
                            
                            <br /> <br />

                            <button style={{ width: 150, height: 50, backgroundColor: 'rgb(100, 236, 150)', borderRadius: 10, border: 'none', resize: 'none', outline: 'none', fontSize: '120%', textAlign: 'center', padding: 5, cursor: 'pointer', fontWeight: 'bold', marginBottom: 10}} onClick={() => loginTapped(setCurrentPage, setStudyGroup, setEvaluationGroup, setAllIdeas)}>Login</button>

                            </>}
                        </div>
                    </Grid>
                </Grid>
            </div>
        </>
    );
}

export default LoginPage;