const HtmlTooltip = styled(({ className, ...props }: TooltipProps) => (
    <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
        backgroundColor: '#f5f5f9fa',
        color: 'rgba(0, 0, 0, 0.87)',
        maxWidth: 220,
        fontSize: theme.typography.pxToRem(12),
        border: '1px solid #dadde9',
    },
}));

// https://stackoverflow.com/a/9160869/4915882
String.prototype.insert = function (index, string) {
    if (index > 0) {
        return this.substring(0, index) + string + this.substr(index);
    }

    return string + this;
};

const getTextOfEditor = (textEditor) => {
    return textEditor.innerText.replaceAll("<br>", "\n").replaceAll("<br>", "\r").replaceAll("&nbsp;", " ");
}

const getTotalTextFromAllTextEditorsSeperatedBySpace = () => {
    const textEditor = document.getElementById("text-editor");
    if (textEditor == undefined || textEditor == null) return "";
    return getTextOfEditor(textEditor);
}

const getTotalNumberOfWordsFromAllTextEditors = () => {
    const text = getTotalTextFromAllTextEditorsSeperatedBySpace();
    const words = text.split(" ");
    let length = 0;
    for (let i = 0; i < words.length; i++) {
        if (words[i] != "") {
            length += 1;
        }
    }
    return length;
}

const setWordCount = (currentPage) => {
    // document.getElementById("word-count").innerHTML = "Number of words: " + getTotalNumberOfWordsFromAllTextEditors() + ((currentPage == "study") ? " / 250" : "");
    document.getElementById("word-count-number").innerHTML = getTotalNumberOfWordsFromAllTextEditors() + "";
}

// populateDashboard
const setDashboardContents = (idea, allIdeas, setAllIdeas, studyGroup, evaluationGroup, currentPage) => {
    if (idea == undefined || idea == null || idea.length < 1) return;
    if (idea[idea.length - 1] != ".") {
        idea += ".";
    }
    if (allIdeas.length <= 2) {
        setAllIdeas([...allIdeas, idea]);
        makeTheLatestRightDashboardPGreyForOneSecond([...allIdeas, idea]);
    } else {
        if ((currentPage == "study" && studyGroup == 2) || (currentPage == "evaluation" && evaluationGroup == 2)) { // only GPT, so shift all and add the new one to the bottom of the list
            for (let i = 0; i < allIdeas.length - 1; i++) {
                allIdeas[i] = allIdeas[i + 1];
            }
            allIdeas[allIdeas.length - 1] = idea;
        } else { // static + GPT, so keep the first 3 and only shift and add in the last 2
            allIdeas[allIdeas.length - 2] = allIdeas[allIdeas.length - 1];
            allIdeas[allIdeas.length - 1] = idea;
        }
        setAllIdeas([...allIdeas]);
        makeTheLatestRightDashboardPGreyForOneSecond([...allIdeas]);
    }
    
    // if (allIdeas.length >= 4) {
    //     setTimeout(() => {
    //         document.getElementById("end-of-dashboard").scrollIntoView();
    //     }, 100);
    // }
}

